import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { AiFillCheckCircle } from 'react-icons/ai';
import cssVar from 'views/mobileView/styles';

export default function PinSuccess(props){

    const TYPE = props.location.state.type
    React.useEffect(() => {
        if(!props.location.state?.type){
			props.history.push('/menu')
		}
	}, []);

    return (
        <div className='mainContainer'>
            <MainHeader
                header={TYPE==='new'?'Set MF transaction PIN':TYPE==='change'?'Change MF transaction PIN':''}
                navigation={props.history}
                backAction={()=>{props.history.push('/menu')}}
            />
            <div className="dataContainer" style={{paddingTop:'42px'}}>
                <div style={{display:"flex",flexDirection:"column",textAlign:"center",gap:"16px",marginTop:"40%"}}>
                    <div><AiFillCheckCircle size={120} className='secondaryGreen'/></div>
                    <div style={{fontSize:'20px',fontWeight:"400",fontColor:cssVar.getPropertyValue('--black')}}>New PIN set</div>
                    <div className='header2dark secondaryGreen' onClick={()=>props.history.push('/menu')}>Go back to menu</div>
                </div>
            </div>
        </div>
    )
}
